import React from "react";
import data from "../../../Time.json";
import { useState, useEffect } from "react";
import cl from "./FreeTime.module.css"
import PeriodOfTime from "./periodOfTime/PeriodOfTime";
import { useCheckboxStore } from "../../../store/CheckboxStore";
import { HeadlineBody } from "../../../textStyles/TextStyleComponents";
import { useNavigate } from "react-router-dom";

const FreeTime = ({ selectedDate }) => {
    const [freeMorningSlots, setFreeMorningSlots] = useState([]);
    const [freeDaySlots, setFreeDaySlots] = useState([]);
    const [freeEveningSlots, setFreeEveningSlots] = useState([]);

    const checkboxStore = useCheckboxStore();

    const [selectedTime, setSelectedTime] = useState(checkboxStore.selectedTime);


    const navigate = useNavigate();

    let tg = window.Telegram.WebApp;

    

    const durationOfServices = checkboxStore.totalSelectedTime;

    const intervalOfTime = 15;

    const currentDate = new Date();
    const currentDay = currentDate.getDate();
    const currentHours = currentDate.getHours();
    const currentMinutes = currentDate.getMinutes();

    function getTimeInMinutes(timeString) {
        const [hours, minutes] = timeString.split(":").map(Number);
        return hours * 60 + minutes;
    }

    const handleTimeSelection = (time) => {
        setSelectedTime(time);
        const endTimeInMinutes = getTimeInMinutes(time) + durationOfServices

        const endHours = Math.floor(endTimeInMinutes / 60);
        const endMinutes = (endTimeInMinutes % 60).toString().padStart(2, '0');

        tg.MainButton.text = "Далее " + time + " - " + endHours + ":" + endMinutes;
        if (!tg.MainButton.isVisible) tg.MainButton.show()
        checkboxStore.setSelectedTime(time);
    };

    tg.MainButton.onClick(() => {
        navigate("/lastStep");
        tg.MainButton.hide();
    });

    useEffect(() => {
        const formattedDate = `${selectedDate.day}.${selectedDate.month + 1}.${selectedDate.year}`;


        if (data.days && data.days[formattedDate]) {
            const slots = data.days[formattedDate];

            const freeSlots = [];

            for (let i = 0; i < slots.length; i++) {
                const currentSlot = slots[i];
                if (currentSlot.status === "free") {
                    freeSlots.push(currentSlot.time);
                }
            }

            const visibleSlots = [];
            const countOfNeededTime = Math.ceil(durationOfServices / intervalOfTime);
            const freeSlotsInMinutes = [];
            for (let i = 0; i < freeSlots.length; i++) {
                freeSlotsInMinutes.push(getTimeInMinutes(freeSlots[i]))
            }

            for (let i = 0; i < freeSlots.length; i++) {
                const currentTimeInMinutes = getTimeInMinutes(freeSlots[i])
                const arrayOfNeededSlotsForCurrentTime = [];
                for (let j = 0; j < countOfNeededTime; j++) {
                    arrayOfNeededSlotsForCurrentTime.push(currentTimeInMinutes + intervalOfTime * j)
                }

                const allExist = arrayOfNeededSlotsForCurrentTime.every(element => freeSlotsInMinutes.includes(element));
                if (allExist) {
                    visibleSlots.push(freeSlots[i])
                }


            }

            if (selectedDate.day === currentDay) {
                const morningSlots = visibleSlots.filter(slot => {
                    const hours = parseInt(slot.split(":")[0]);
                    const minutes = parseInt(slot.split(":")[1]);
                    const currentTime = currentHours * 60 + currentMinutes;
                    const time = hours * 60 + minutes;
                    return hours < 12 && time >= currentTime;
                });

                const daySlots = visibleSlots.filter(slot => {
                    const hours = parseInt(slot.split(":")[0]);
                    const minutes = parseInt(slot.split(":")[1]);
                    const currentTime = currentHours * 60 + currentMinutes;
                    const time = hours * 60 + minutes;
                    return hours >= 12 && hours < 18 && time >= currentTime;
                });

                const eveningSlots = visibleSlots.filter(slot => {
                    const hours = parseInt(slot.split(":")[0]);
                    const minutes = parseInt(slot.split(":")[1]);
                    const currentTime = currentHours * 60 + currentMinutes;
                    const time = hours * 60 + minutes;
                    return hours >= 18 && time >= currentTime;
                });
                setFreeMorningSlots(morningSlots);
                setFreeDaySlots(daySlots);
                setFreeEveningSlots(eveningSlots);

            } else {

                const morningSlots = visibleSlots.filter(slot => {
                    const time = parseInt(slot.split(":")[0]);
                    return time < 12;
                });

                const daySlots = visibleSlots.filter(slot => {
                    const time = parseInt(slot.split(":")[0]);
                    return time >= 12 && time < 18;
                });

                const eveningSlots = visibleSlots.filter(slot => {
                    const time = parseInt(slot.split(":")[0]);
                    return time >= 18;
                });
                setFreeMorningSlots(morningSlots);
                setFreeDaySlots(daySlots);
                setFreeEveningSlots(eveningSlots);
            }


        } else {
            setFreeMorningSlots([]);
            setFreeDaySlots([]);
            setFreeEveningSlots([]);
        }


        if (checkboxStore.lastSelectedDate !== selectedDate) {
            checkboxStore.clearSelectedTime();
            checkboxStore.setLastSelectedDate(selectedDate);
        }

        // Установите выбранное время из хранилища
        setSelectedTime(checkboxStore.selectedTime);

    }, [selectedDate, checkboxStore]);

    return (
        <div>
            <PeriodOfTime freeSlots={freeMorningSlots} name={"morning"} onTimeSelect={handleTimeSelection} selectedTime={checkboxStore.selectedTime}>Утро</PeriodOfTime>
            <PeriodOfTime freeSlots={freeDaySlots} name={"day"} onTimeSelect={handleTimeSelection} selectedTime={checkboxStore.selectedTime}>День</PeriodOfTime>
            <PeriodOfTime freeSlots={freeEveningSlots} name={"evening"} onTimeSelect={handleTimeSelection} selectedTime={checkboxStore.selectedTime}>Вечер</PeriodOfTime>
            {freeMorningSlots.length === 0 && freeDaySlots.length === 0 && freeEveningSlots.length === 0 && (
                <HeadlineBody>В выбранный день нет свободного времени</HeadlineBody>
            )}
        </div>
    )
}

export default FreeTime